

.container{
  height: 100vh;
}

.rutaUnder{
  width: 100%;
  height: 0;
  background: rgba(0,0,0,0.9);
  position: absolute;
  bottom:0;
}

.rutaUnder-enter-active{
  height: 30px;
  -webkit-transition:height 200ms ease-out;
  -o-transition:height 200ms ease-out;
  transition:height 200ms ease-out;
}

.rutaUnder-enter-done{
  height: 30px;
  position: absolute;

  bottom:0;
}

.rutaUnder-exit{
  height: 30px;
  position: absolute;
  bottom:0;
}

.rutaUnder-exit-active{
  height: 0px;
  position: absolute;
  bottom:0;
  -webkit-transition:height 200ms ease-out;
  -o-transition:height 200ms ease-out;
  transition:height 200ms ease-out;
}

.rutaUnder-exit-done{
  height: 0px;
  position: absolute;
  bottom:0;
}

.ruta{
  width: 100%;
  height: 0px;
  background: rgba(0,0,0,0.9);
  position: absolute;
  background:"red"
}


.ruta-enter-active {
  height: 20px;
  -webkit-transition:height 200ms ease-out;
  -o-transition:height 200ms ease-out;
  transition:height 200ms ease-out;
}

.ruta-enter-done{
  height: 20px;

}

.ruta-exit{
  height: 20px;

}

.ruta-exit-active{
  height: 0px;
  -webkit-transition:height 200ms ease-out;
  -o-transition:height 200ms ease-out;
  transition:height 200ms ease-out;

}

.ruta-exit-done{
  height: 0px;

}

.startBtn{
  position: relative;
  top:300px;
}

.cartbtn {
  text-align: center;
  position:relative;
  display:block;
  font-size: 1.7em;
  padding:10px;
  opacity: 0;
  top:-100px;
  width:300px;
  margin: auto;
  color:rgba(0,0,0,0.7);
  font-family: 'Montserrat', sans-serif;
  cursor: pointer;
}

.fade-appear-active {
  top:0px;
  opacity: 1;
  -webkit-transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
  -o-transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
  transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
}

.fade-enter-done{

  opacity: 1;
  top:0px;

  -webkit-transition: color, -webkit-transform 0.4s;

  transition: color, -webkit-transform 0.4s;

  -o-transition: transform 0.4s, color;

  transition: transform 0.4s, color;

  transition: transform 0.4s, color, -webkit-transform 0.4s;
}


.cartbtn:hover{
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9);
  color:rgba(255,255,255,0.7)
}

.fade-exit{
  position:relative;
  display: block;
  font-size: 1.2em;
  opacity: 1;
  top:0px;
  margin: auto;
  font-family: 'Montserrat', sans-serif;
  border:none;
  cursor:pointer;
  -webkit-transition: -webkit-transform 0.4s;
  transition: -webkit-transform 0.4s;
  -o-transition: transform 0.4s;
  transition: transform 0.4s;
  transition: transform 0.4s, -webkit-transform 0.4s;
}

.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
  -o-transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
  transition: opacity 900ms ease-in-out, top 700ms ease-in-out;
}

.fade-exit-done{
  opacity: 0;
}


#viewCart:hover{
  -webkit-transform: scale(0.9);
      -ms-transform: scale(0.9);
          transform: scale(0.9)
}

#viewCart:focus{
  outline: none;
}


.line-enter{
  margin: auto;
  width:0px;
  height: 2px;
  background: rgba(255,255,255,0.7);
}

.line-enter.line-enter-active{
  margin: auto;
  width:140px;
  height: 2px;
  background: rgba(255,255,255,0.7);
  -webkit-transition: width 400ms ease-out ;
  -o-transition: width 400ms ease-out ;
  transition: width 400ms ease-out ;

}

.line-enter-done{
  margin: auto;
  width:140px;
  height: 2px;
  background: rgba(255,255,255,0.7);
}





